ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
li {
  float:left; 
}
.dropbtn {
  width: 30px;
  height: 30px;
  border-radius: 3px;
  background: none;
  border: none;
}
.dropbtn:hover{
  transform: rotate(90deg);
}

li a:hover, .dropdown:hover {
  color: red;
  
}
li.dropdown {
  display: inline-block;
  
}
.dropdown-content {
  display: none;
  position: absolute;
  background-color:peru;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.9);
  z-index: 1;
}
.dropdown-content a {
  color: white;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}
.dropdown-content a:hover {
  background-color: #f1f1f1;
  border-radius: 10px;
}
.dropdown:hover .dropdown-content {
  display:block;
  border: none;
  border-radius: 10px;
}
