
.header{
  background-color: peru;
  border-radius: 5px;
  grid-column: 1 / span 3;
}
a:link, a:visited {
  color:white;
  padding: 0px 15px;
  text-align: center;
  text-decoration: none;
}
a:hover, a:active {
  color: red;
}
@media only screen and (max-width:550px){
  .header{
      grid-column: 1 / span 2;
  }
  .navbar{
    display: none;
  } 
}
